:root {
  --primary: #8b1533;
  --accent: #ff6f8e;
  --muted: #6b6b6b;
  --bg: #fbf7f5;
  --card: #fff;
  --radius: 12px;
}

.navbar { 
  display:flex; 
  justify-content:space-between; 
  align-items:center; 
  padding:14px 16px; 
  position:sticky; 
  top:0; 
  background:#fff; 
  z-index:100; 
  border-bottom:1px solid #f1e6e9; 
}

.logo { 
  height:52px; 
  border-radius:10px; 
}

.product-grid { 
  display:grid; 
  grid-template-columns:repeat(4,1fr); 
  gap:18px; 
  margin-top:12px; 
}

.card-container { 
  display:grid; 
  grid-template-columns:repeat(auto-fill,minmax(250px,1fr)); 
  gap:18px; 
}

.card { 
  background: var(--card); 
  border-radius: var(--radius); 
  overflow: hidden; 
  box-shadow: 0 2px 8px rgba(0,0,0,0.1); 
  transition: transform 0.2s ease; 
}

.card:hover { 
  transform: translateY(-2px); 
  box-shadow: 0 4px 16px rgba(0,0,0,0.15); 
}

.card img { 
  width:100%; 
  height:220px; 
  object-fit:cover; 
  display:block; 
  border-radius:10px; 
}

.card-body { 
  padding: 16px; 
}

.card-title { 
  font-size: 18px; 
  font-weight: 600; 
  color: var(--primary); 
  margin-bottom: 8px; 
}

.btn { 
  background: var(--primary); 
  color: white; 
  border: none; 
  padding: 10px 20px; 
  border-radius: 8px; 
  cursor: pointer; 
  font-weight: 600; 
  transition: background 0.2s ease; 
}

.btn:hover { 
  background: #6b0f26; 
}

.btn:disabled { 
  background: #ccc; 
  cursor: not-allowed; 
}

.meta { 
  padding:12px; 
}

.container { 
  max-width: 1200px; 
  margin: 0 auto; 
  padding: 0 16px; 
}

@media (max-width:980px){ 
  .product-grid { 
    grid-template-columns:repeat(2,1fr); 
  } 
}

@media (max-width:600px){ 
  .product-grid { 
    grid-template-columns:1fr; 
    gap:12px; 
  } 
  .card img { 
    height:180px; 
  } 
}

.page { 
  min-height: calc(100vh - 72px); 
  padding: 28px 16px; 
}
